.App {
  text-align: center;
}

#target {
  background-color: black;
  /* text-shadow:1px 1px 10px #fff, 1px 1px 1px aqua; */
}


@keyframes slideRight {
  from {
    transform: translate(-100%, 0);
  }

  to {
    transform: translate(0, 0);
  }
}

#desc {
  background-color: black;
}

@keyframes slideIn {
  from {
    transform: translate(100%, 0);
  }

  to {
    transform: translate(0, 0);
  }
}

